import React, { PureComponent } from 'react';
import debounce from 'lodash/debounce'

import { Formik, Form } from 'formik';
import md5 from 'blueimp-md5';

import Box from 'components/Box';
import Text from 'components/Text';
import Container from 'components/Container';
import Circle from 'components/Circle';
import Button from 'components/Button';
import Flex from 'components/Flex';
import Input from 'components/Input';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import Title from '../Title'
import { askings } from '../../NoticePage'

const nameId = askings.map((name) => md5(name))
const initialValue = nameId.reduce((iv, name) => Object.assign(iv, { [name]: '' }), {})

const Content = ({ children }) => (
  <Box
    mx={mobileOrDesktop('1em', '0.5em')}
    py="2em"
    boxShadow="0px 3px 4px 4px rgba(0,0,0,0.1)"
    bg="white"
    borderRadius="0 0 1em 1em"
  >
    <Title my="1em" title="我的看診回饋" />
    <Container>
      {children}
    </Container>
  </Box>
)

class Note extends PureComponent {
  constructor(props) {
    super(props)
    this.handleStore = debounce(this.handleStore, 500).bind(this)
  }

  state = {
    confirm: true
  }

  handleStore = target => this.props.onStore(target)

  handleConfirm = () => {
    this.setState({ confirm: true })
  }

  handleModify = () => {
    this.setState({ confirm: false })
  }

  render() {
    const { notes } = this.props
    const { confirm } = this.state
    if (confirm && notes !== undefined) {
      return (
        <Content>
          <Flex flexWrap="wrap" mx="0.5em" mt="1em">
            {askings.map((a, i) => {
              const name = nameId[i]
              return (
                <Box pt={mobileOrDesktop(i > 0 && '0.5em', 0)} pb={mobileOrDesktop(i > 0 && '0.5em', '1em')} width={mobileOrDesktop(1, 1/3)} key={i} borderLeft="2px solid" borderColor="lightBgPurple" position="relative">
                  <Box mx="1em">
                    <Text fontSize={mobileOrDesktop('1.2em', '1em')} color="titleBlue">{a}</Text>
                    {notes !== undefined && <Text fontSize="1.5em" whiteSpace="pre-wrap">{notes[name]}</Text>}
                  </Box>
                  <Box position="absolute" top={mobileOrDesktop(i > 0 ? '0.75em' : '0em', '0em')} left="0" transform="translateX(-55%)" width="1em">
                    <Circle bg="lightBgPurple" />
                  </Box>
                </Box>
              )})}
          </Flex>
          <Box textAlign="center" mt="1em">
            <Button onClick={this.handleModify}>修改</Button>
          </Box>
        </Content>
      )
    }
    return (
      <Content>
        <Formik
          initialValues={Object.assign({}, initialValue, notes)}
          onSubmit={() => {
            this.handleConfirm()
          }}
        >
          {({ handleChange, values }) => (
            <Form>
              <Flex flexWrap="wrap">
                {askings.map((a, i) => {
                  const name = nameId[i]
                  return (
                    <Box width={mobileOrDesktop(1, 1/3)} mt="0.5em" key={i}>
                      <Box mx="1em">
                        <Text my="0.5em" fontSize={mobileOrDesktop('1.2em', '1em')}>{a}</Text>
                        <Input
                          textarea
                          placeholder="請輸入"
                          onChange={(e) => {
                            handleChange(e)
                            this.handleStore(e.target)
                            this.handleModify()
                          }}
                          onBlur={e => {
                            handleChange(e)
                            this.props.onStore(e.target)
                          }}
                          name={name}
                          value={values[name]}
                        />
                      </Box>
                    </Box>
                  )})}
              </Flex>
              <Box textAlign="center" mt="1em">
                <Button type="submit">儲存</Button>
              </Box>
            </Form>
          )}
        </Formik>
      </Content>
    );
  }
}

export default Note;
