import React from 'react';

import Box from 'components/Box';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import MemoResult from '../MemoResult';

const Memo = ({ memo, onEdit, withDownload }) => (
  <Box
    mx={mobileOrDesktop('1em', '0.5em')}
    py={mobileOrDesktop('2em', '1em')}
    boxShadow="0px 3px 4px 4px rgba(0,0,0,0.1)"
    bg="white"
    borderRadius="0 0 1em 1em"
  >
    <MemoResult memo={memo} onEdit={onEdit} withDownload={withDownload} />
  </Box>
);

export default Memo;
