import React from 'react';
import { navigate } from 'gatsby'

import storingHoc from '../storingHoc'
import Step1 from '../Steps/Step1'
import Step2 from '../Steps/Step2'
import Step3 from '../Steps/Step3'

const Edit = ({
  hid,
  step,
  newDot,
  setDotData,
  removeDot,
  setAnswer,
  memos,
  setYesNo,
}) => {
  const memoIndex = hid - 1
  const memo = memos[memoIndex]
  const actions = {
    newDot: (payload) => newDot({ position: payload, memoIndex }),
    setDotData: (payload) => setDotData({ ...payload, memoIndex }),
    removeDot: (payload) => removeDot({ index: payload, memoIndex }),
    setAnswer: (payload) => setAnswer({ ...payload, memoIndex }),
    setYesNo: (payload) => setYesNo({ ...payload, memoIndex }),
  }
  if (step === '1') return <Step1 {...actions} {...memo} onNext={() => navigate(`/memo/history/${hid}/edit/2`)} />
  if (step === '2') return <Step2 {...actions} {...memo} onNext={() => navigate(`/memo/history/${hid}/edit/3`)} />
  if (step === '3') return <Step3 {...actions} {...memo} onNext={() => navigate(`/memo/history/${hid}`)} onEdit={() => navigate(`/memo/history/${hid}/edit/1`)} />
  navigate(`/memo/history/${hid}`)
  return null;
};

export default storingHoc(Edit);
