import React, { useState } from 'react';
import { compose } from 'redux'
import ItemsCarousel from 'react-items-carousel';
import { navigate } from 'gatsby'
import { IoMdArrowDropleft, IoMdArrowDropright } from 'react-icons/io'
import ReactHoverObserver from 'react-hover-observer';

import FullpageVerticalCenter from 'components/FullpageVerticalCenter';
import Container from 'components/Container';
import Box from 'components/Box';
import Flex from 'components/Flex';
import Link from 'components/Link';
import Button from 'components/Button';
import Circle from 'components/Circle';

import { mobileOrDesktop } from 'components/ThemeProvider/theme';
import withResponsive from 'hoc/withResponsive'

import Layout from '../../Layout'
import storingHoc from '../storingHoc'
import Title from '../Title';
import BgDeco from '../BgDeco';
import BodyCard from './BodyCard'

const MemoPage = ({ memos, isMobile }) => {
  const [activeItemIndex, setActive] = useState(0)
  const carousel = (
    <ItemsCarousel
      numberOfCards={isMobile ? 1 : 3}
      activeItemIndex={activeItemIndex}
      requestToChangeActive={setActive}
      chevronWidth={isMobile ? 32 : 48}
      leftChevron={<Circle width="100%" is={p => <Button.circle color="primary" {...p} />}><IoMdArrowDropleft size={isMobile ? 24 : 32} /></Circle>}
      rightChevron={<Circle width="100%" is={p => <Button.circle color="primary" {...p} />}><IoMdArrowDropright size={isMobile ? 24 : 32} /></Circle>}
    >
      {memos.map((memo, i) => (
        <Box px={!isMobile && i !== activeItemIndex + 1 && '10%'} key={i}>
          <ReactHoverObserver>
            {({ isHovering }) => (
              <BodyCard
                onClick={() => navigate(`/memo/history/${i + 1}`)}
                opacity={isMobile || i === activeItemIndex + 1 || isHovering ? 1 : 0.5}
                {...memo}
              />
            )}
          </ReactHoverObserver>
        </Box>
      ))}
    </ItemsCarousel>
  )

  const content = (
    <Container position="relative" py="2em" textAlign="center">
      <Title title="我的看診備忘錄" subtitle={isMobile ? '你之前新增的備忘錄如下，\n點擊進入就可以繼續編輯' : '你之前新增的備忘錄如下，點擊進入就可以繼續編輯'} />
      <Box mt={mobileOrDesktop('1.5em', '2.75em')}>
        {isMobile ? (
          memos.length > 1 ? carousel : <BodyCard onClick={() => navigate('/memo/history/1')} {...memos[0]} />
        ) : (
          memos.length > 3 ? carousel : (
            <Flex justifyContent="center" width="100%">
              {memos.map((memo, i) => (
                <Box width={1 / 3} key={i} px={memos.length > 2 && i !== activeItemIndex + 1 && '10%'}>
                  <ReactHoverObserver>
                    {({ isHovering }) => (
                      <BodyCard
                        onClick={() => navigate(`/memo/history/${i + 1}`)}
                        opacity={memos.length < 3 || i === activeItemIndex + 1 || isHovering ? 1 : 0.5}
                        {...memo}
                      />
                    )}
                  </ReactHoverObserver>
                </Box>
              ))}
            </Flex>
          )
        )}
      </Box>
      <Box mt={mobileOrDesktop('1.5em', '2.75em')}>
        <Button is={Link} to="/memo/new/1">製作新的備忘錄</Button>
      </Box>
    </Container>
  )
  return (
    <Layout>
      <Box position="relative">
        {!isMobile && <BgDeco />}
        {isMobile ? content : (
          <FullpageVerticalCenter >
            {content}
          </FullpageVerticalCenter>
        )}
      </Box>
    </Layout>
  );
}

export default compose(withResponsive, storingHoc)(MemoPage);
