import React from 'react';

import Box from 'components/Box'
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import BodyPoints from '../BodyPoints'
import DateCard from '../DateCard'

const BodyCard = ({ dots, createdAt, answers, useSvg, ...props }) => {
  return (
    <Box {...props}>
      <Box position="relative" pb="0.5em" width="100%" px={mobileOrDesktop('3em', 0)}>
        <DateCard date={createdAt}>
          <Box
            bg="bodyBgOrange"
            p="1em"
            m="1em"
            borderRadius="1em"
          >
            <BodyPoints useSvg={useSvg} dots={dots} />
          </Box>
        </DateCard>
      </Box>
    </Box>
  );
};

export default BodyCard;
