import React, { PureComponent } from 'react';

import Progress from 'components/Progress';
import Container from 'components/Container';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import Layout from '../../Layout';
import MemoResult from '../MemoResult';
import StepNumber from './StepNumber'
import DateCard from '../DateCard'

class Step3 extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      dots: props.dots,
      yesNo: props.yesNo,
      answers: props.answers,
    }
  }

  handleComfirm = () => {
    const { dots, answers, yesNo, addMemo, clearData, onNext } = this.props
    onNext()
    if (addMemo) addMemo({ answers, dots, yesNo })
    if (clearData) clearData()
  }

  render() {
    const { dots, yesNo, answers } = this.state
    return (
      <Layout bg="lightBgOrange" position="relative" headerBg={mobileOrDesktop('lightBgOrange', 'auto')}>
        <StepNumber>3</StepNumber>
        <Container py="2em" position="relative">
          <Progress step={3} />
          <DateCard>
            <MemoResult
              memo={{ dots, yesNo, answers }}
              onConfirm={this.handleComfirm}
              onEdit={this.props.onEdit}
            />
          </DateCard>
        </Container>
      </Layout>
    );
  }
}

export default Step3
